.projects-slider {
  padding: 32px 0;
  background-color: $gray-medium;
  margin-bottom: 16px;

  @media #{$breakpointTablet} {
    padding: 32px 0;
  }

  @media #{$breakpointMobile} {
    padding: 32px 0;
  }
}

.projects-slider__item {
  position: relative;
  display: block;
  overflow: hidden;
  height: 320px;
  padding: 0;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
  height: calc((100vw / 3 - 24px) * 0.57);

  @media #{$breakpointMobile} {
    height: calc((100vw / 2 - 16px) * 0.57);
  }

  @media #{$breakpointMobileXS} {
    height: calc(100vw * 0.57);
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: linear-gradient(to top, rgba($gray-darkest, 0.7) 0%, rgba($gray-darkest, 0) 40%);
    z-index: 1;
    transition: all 0.5s ease-in-out;
  }

  &:hover::before,
  &:focus::before {
    opacity: 0;
  }

  img {
    height: 100%;
    object-fit: cover;
    object-position: center;
    transition: all 0.5s ease-in-out;
  }

  &:hover img,
  &:focus img {
    transform: scale(1.05);
  }
}

.projects-slider__text {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;

  p {
    color: $white;
    font-size: 24px;
    line-height: 1.5;
    text-align: center;
    padding: 16px 32px 32px;

    @media #{$breakpointTablet} {
      padding: 16px;
    }

    @media #{$breakpointMobileXS} {
      padding: 16px 8px;
      font-size: 20px;
    }
  }
}
