.scroll-top {
  position: fixed;
  right: 40px;
  bottom: 40px;
  width: 60px;
  height: 60px;
  background-color: rgba($secondary, 0.7);
  border: 0;
  outline: 0;
  transition: all 0.2s ease-in;
  cursor: pointer;
  border-radius: 50%;

  &:hover,
  &:focus {
    background-color: rgba($secondary, 1);
  }

  &--hidden {
    opacity: 0;
    visibility: hidden;
  }
}
